import {
		mapState,  
    } from 'vuex';
import md5 from '../../kayida/common/md5.js'

export default {
	computed: {
	            ...mapState(['userInfo'])  
	        },
	data() {
		return {
			url:'',
			pic:'',
			share:0,
			popup:0,
			describe:'',
			type:'',
		}
	},
	methods: {
		downPromote() {
			this.popup = 1
		},
		copyText() {
			uni.setClipboardData({
			    data: this.text,
			    success: function () {
					uni.showToast({
						title: '复制成功',
						duration:2000,
						icon:"none"
					})
			    }
			});
		},
		downPic() {
			uni.downloadFile({
				url: this.pic,
				success: (res) =>{
					if (res.statusCode === 200){
						uni.saveImageToPhotosAlbum({
							filePath: res.tempFilePath,
							success: function() {
								uni.showToast({
									title: "保存成功",
									icon: "none"
								});
							},
							fail: function() {
								uni.showToast({
									title: "保存失败，请稍后重试",
									icon: "none"
								});
							}
						});
					}
				}
			})
			this.popup = 0
		},
		canceldown() {
			this.popup = 0
		},
		shareWeixin() {
			uni.share({
			    provider: "weixin",
			    scene: "WXSceneSession",
			    type: 2,
			    imageUrl: this.pic,
			    success: function (res) {
			        uni.showToast({
			        	title:"分享成功",
						icon:"none"
			        });
			    },
			    fail: function (err) {
			        uni.showToast({
			        	title:"分享失败",
			        	icon:"none"
			        });
			    }
			});
			this.share = 0
		},
		shareFriends() {
			uni.share({
			    provider: "weixin",
			    scene: "WXSenceTimeline",
			    type: 2,
			    imageUrl: this.pic,
			    success: function (res) {
			        uni.showToast({
			        	title:"分享成功",
			    		icon:"none"
			        });
			    },
			    fail: function (err) {
			        uni.showToast({
			        	title:"分享失败",
			        	icon:"none"
			        });
			    }
			});
			this.share = 0
		},
		shareCancel() {
			this.share = 0
		},
		copy() {
			uni.setClipboardData({
			    data: this.url,
			    success: function () {
					uni.showToast({
						title: '复制成功',
						duration:2000,
						icon:"none"
					})
			    },
				fail:function(res){
				}
			});
		},
		shareDetail() {
			this.share = 1
			uni.setClipboardData({
			    data: this.describe,
			    success: function () {
					uni.showToast({
						title: '复制成功',
						duration:2000,
						icon:"none"
					})
			    },
				fail(res) {
				}
			});



		},
	},
	onLoad(option) {
		this.type = option.type
		this.id = option.id
		let data = {
			id: option.id,
			user_id: this.userInfo.user_id,
			token: this.userInfo.token,
			sign: md5(this.userInfo.user_id+this.userInfo.token)
		}
		if(this.type == 'apply'){
			this.request.httpRequest('api/card/poster',data,'GET').then(res => {
				this.pic = res.data.retval.poster
				this.url = res.data.retval.short_url
				this.describe = res.data.retval.describe
			})
		}else if(this.type == 'loan'){
			let loan = option.loan
			if(loan == 0){
				this.request.httpRequest('api/loan/poster',data,'GET').then(res => {
					this.pic = res.data.retval.poster
					this.url = res.data.retval.short_url
					this.describe = res.data.retval.describe
				})
			}else{
				this.request.httpRequest(`api/loan${loan}/poster`,data,'GET').then(res => {
					this.pic = res.data.retval.poster
					this.url = res.data.retval.short_url
					this.describe = res.data.retval.describe
				})
			}
		}else if(this.type == 'credit'){
			let data = {
				user_id: this.userInfo.user_id,
				token: this.userInfo.token,
				sign: md5(this.userInfo.user_id+this.userInfo.token)
			}
			this.request.httpRequest('api/credit/poster',data,'GET').then(res => {
				this.pic = res.data.retval.poster
				this.url = res.data.retval.short_url
				this.describe = res.data.retval.describe
			})
		}
	},
}




































